.til-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 1em;
}

.til-header {
  text-align: center;

    font-size: ms(1);
  margin-bottom: 1.5em;
}

.til-list {
  list-style: none;
  padding: 0;

  .til-item {
    margin-bottom: 1.5em;

    .til-title {
      font-size: 1.2em;
      color: $primary-color;

      &:hover {
        text-decoration: underline;
      }
    }

    .til-date {
      font-size: 0.9em;
      color: $secondary-color;
    }
  }
}
