// Typography
@import url("https://fonts.googleapis.com/css2?family=Fira+Sans:wght@100;200;400&display=swap");

$base-font-family: "Fira Sans", sans-serif;
$heading-font-family: $base-font-family;

// Font Sizes

$footnote-font-size: .8em;

// Line height
$line-height-mobile: 1.9rem;
$line-height-desktop: 2.03rem;
$heading-line-height: 1.2;

// Other Sizes
$base-border-radius: 0.52em;
$base-spacing: $line-height-mobile * 1;
$small-spacing: $base-spacing / 2;
$base-z-index: 0;

// Colors
$primary-color: #000;
$action-color: $primary-color;
$white-color: #fff;
$black-color: #000;
$blue-color: #0086b3;
$secondary-color: $blue-color; // aliasing
$light-blue-color: rgba(0, 134, 179, .5);
$dark-gray-color: #333;
$medium-gray-color: #999;
$light-gray-color: #ccc;
$very-light-gray-color: #fafafa;

// Font Colors
$base-font-color: rgba($dark-gray-color, 0.8);

// Border
$base-border-color: $light-gray-color;
$base-border: 1px solid $base-border-color;

// Background Colors
$base-background-color: $very-light-gray-color;
$secondary-background-color: tint($base-border-color, 80%);

// Forms
$form-box-shadow: inset 0 1px 3px rgba($black-color, 0.06);
$form-box-shadow-focus: $form-box-shadow,
  0 0 5px adjust-color($action-color, $lightness: -5%, $alpha: -0.3);

// Animations
$base-duration: 150ms;
$base-timing: ease;
