* {
  &,
  &:before,
  &:after {
    box-sizing: border-box;
  }
}

html,
body {
  width: 100%;
  height: 100%;
}

.clearfix {
  &:before {
    content: " ";
    display: table;
  }

  &:after {
    content: " ";
    display: table;
    clear: both;
  }

  *zoom: 1;
}

body {
  background-color: $base-background-color;
}

main {
  min-height: 100%;
  min-width: 100%;

  display: flex;
  flex-flow: column;

  padding-left: ms(0);
  padding-right: ms(0);

  @media (min-width: 720px) {
    padding-left: ms(4);
    padding-right: ms(3);
    
    padding-bottom: ms(2)
  };
}

#header-container {
  width: 100%;
  
  display: flex;
  align-items: center;
  
  @include ms-respond(padding-top, 1);

  @media (max-width: 720px) {
    flex-flow: row wrap;
    
    @include ms-respond(padding-bottom, -2);
  }

  @media (min-width: 720px) {
    @include ms-respond(padding-bottom, 1);
  }
}

.page-container, .post-container {
  @media (min-width: 720px) {
    @include ms-respond(max-width, 9);
  }
}

.post-container {
  @include ms-respond(padding-bottom, 2);
}

hr {
  border-top: none;
  border-bottom: 2px solid lighten($light-gray-color, 7%);
  width: 100%;
  margin: $small-spacing 0;
}

.container {
  @media (max-width: 720px) {
    padding: ms(-2);
  }
  @media (min-width: 38rem) {
    padding: ms(2);
  }
  //padding: $base-spacing;
  margin-left: auto;
  margin-right: auto;
  max-width: 55rem;
  font-weight: normal;
}

.pull-left {
  float: left;
}

.pull-right {
  float: right;
}

.link {
  color: $blue-color;
  padding-bottom: .05em;
  border-bottom: 2px solid transparent;

  @include transition(border 300ms linear);

  &:hover,
  &:focus,
  &:active {
    border-bottom: 2px solid $blue-color;
  }
}

div.opening {
  position: relative;
  display: flex;
  align-items: flex-end;
  justify-content: center;

  p {
    display: block;
    margin: 0 0 -.25em 0;
  }

  div.titles-pre {

    p {
      position: relative;
      bottom: 0;
    }


  }

  div.titles-content {
    position: relative;
  }

  div.titles-content::after {
    content: '';
    display: block;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    background: linear-gradient(#fafafa 0%, rgba(250,250,250,.65) 55%, rgba(250,250,250,0) 75%);
  }
}

div.titles {
  margin-right: 0.27em;
}


 header.site-header {
  display: flex;
  margin-inline-end: ms(5);

  color: $blue-color;
  
  cursor: default;
  
  @media (max-width: 720px) {
    min-width: 100%;
    height: 60%;

    & :first-child {
      margin-top: auto;
    }

   @include ms-respond(margin-bottom, 1);
   margin-block-end: ms(2);
  }
 }

.intro {
  .author-name {
    display: inline-block;
    padding: 0 .2em;
    background: lighten($light-gray-color, 15%)
  }

  .container {
    padding-bottom: 0;
  }
}

nav {
  display: flex;
  font-weight: 200;

  justify-content: space-around;
  min-width: 100%;
  
  @media (min-width: 720px) {
    min-width: unset;
    justify-content: unset;
  }

  // pointer-events: all;
  // cursor: pointer;
  
  ul {
    display: flex;
    flex-flow: column;
    text-align: center;
    
    margin: 0;
    padding: 0;
  }

  li {
    display: inline-block;

    @include ms-respond(margin-right, 0);
    @include ms-respond(margin-bottom, -1);
    
    @media (min-width: 720px) {
      // why can't I `initial`?
      display: block;
    }
    
    margin-bottom: 0px !important;
  }
}

.category-title {
  margin: 1em 0 0.5em 0;

  &:first-child {
    margin-top: 0;
  }
}

.post-item {
  margin-bottom: 0.2em;
  display: block;

  a {
    color: $action-color;
  }

  .article-title {
    display: inline-block;
    width: 85%;
    //border-bottom: 1px dashed lighten($light-gray-color, 10%);
  }

  .date-label {
    //border: 1px solid lighten($light-gray-color, 10%);
    padding: 0 0.2em;
    @extend .pull-right;
  }

}

.post-meta {
  color: $medium-gray-color;
  text-transform: uppercase;
  display: inline-block;
  letter-spacing: 1px;
  font-size: rem(14);
  margin-bottom: 1px;
}

.post {
  @extend .clearfix;
}

.pagination {
  li, a, span {
    display: inline-block;
  }

  a, span {
    font-size: rem(12);
    padding: .5em;
  }

  .prev, .next {
    @extend .link;
  }
}

.share {
  border-top: 0.2em dashed lighten($light-gray-color, 13%);
  padding-top: $base-spacing;
  margin-top: $base-spacing * 2;

  p {
    margin: 0 5em;
    font-size: rem(14);
    color: $medium-gray-color;
  }
}

.disqus {
  background-color: lighten($light-gray-color, 16%);
  padding: $base-spacing 0;

  hr {
    margin: $base-spacing 0;
  }
}

.site-footer {
  @extend .clearfix;

  padding: $base-spacing 0;

  small {
    display: block;
    font-size: rem(12);
    color: darken($medium-gray-color, 10%);
  }

  .block {
    display: block;
  }
}

.video-container {
	position:relative;
	padding-bottom:56.25%;
	padding-top:30px;
	height:0;
	overflow:hidden;

  iframe,
  object,
  embed {
    position:absolute;
  	top:0;
  	left:0;
  	width:100%;
  	height:100%;
  }
}

.divider {
  border-top: 0.2em solid lighten($light-gray-color, 13%);
  margin-bottom: $base-spacing * 2;
  margin-top: $base-spacing * 2;
}

// This applies from 0px to 38rem
@media (max-width: 720px) {
  .post-list {
      @include ms-respond(margin-top, 1);
    // margin-bottom: ms(1);
  }
  
  .date-label {
    float: left !important;
    border: 0 !important;
    margin-left: -2px !important;
    margin-top: 2px;
    margin-right: 1em;
  }

  .article-title {
    border-bottom: none !important;
  }

  .post-link {
    &:hover,
    &:focus,
    &:active {
      border-bottom: 2px solid transparent;
      color: $blue-color;
    }
  }
}

.post-image {
  margin-bottom: ms(1);
}
