#app {
  textarea {
    width: 100%;
    padding: 10px;
    font-family: monospace;
    font-size: 14px;
    margin-bottom: 10px;
  }

  button {
    padding: 10px 20px;
    cursor: pointer;
  }

  pre {
    background-color: #f4f4f4;
    padding: 10px;
    border: 1px solid #ddd;
    white-space: pre-wrap;
  }
}