body {
  @include ms-respond(font-size, 0);

  @include ms-respond(line-height, 0);

  @media (max-width: 720px) {
    line-height: $line-height-mobile;
  }
  @media (min-width: 720px) {
    line-height: $line-height-desktop;
  }

  color: $base-font-color;
  font-family: $base-font-family;
}

@font-face {
    font-family: "Kaushan Script";
    src: url("/assets/fonts/KaushanScript-Regular.ttf");
}

h1
// h2,
// h3,
// h4,
// h5,
// h6 {
  {
  line-height: $heading-line-height;
  margin: $small-spacing 0 $small-spacing 0;
  
  a {
    text-decoration: none;
  }
  
  a:hover {
    text-decoration: none;
    color: unset;
  }
}

h1 {
  @include ms-respond(font-size, 3);
  
  text-align: center;
}

h2 {
  @include ms-respond(line-height, 2);
  @include ms-respond(font-size, 1);
}

.post-title {
  @include ms-respond(margin-bottom, 2);
  margin-top: 0;
  color: $dark-gray-color;
}

p + h2 {
  @include ms-respond(margin-top, 2);
}

h3 {
  @include ms-respond(font-size, 1);
}

//h4 {
//  @include ms-respond(font-size, 2);
//}
//
//h5 {
//  @include ms-respond(font-size, 1);
//}

h6 {
  @include ms-respond(font-size, 0);
}

p {
  // margin: 0 0 ms(1);
  @include ms-respond(margin-bottom, 0);

  &.lead {
    font-size: 1.375rem;
    font-weight: 300;
  }

  &.post-reminder {
    font-size: ms(-1);
    margin: 0;
  }
}

a {
  color: $action-color;
  text-decoration: none;
  transition: color $base-duration $base-timing;

  &:active,
  &:focus,
  &:hover {
    color: shade($action-color, 25%);
  }
}

hr {
  border-bottom: $base-border;
  border-left: 0;
  border-right: 0;
  border-top: 0;
  margin: $base-spacing 0;
}

img,
picture {
  margin: 0;
  max-width: 100%;
}

blockquote {
  padding: ms(-4);
  margin: ms(-2) 0;
  color: $dark-gray-color;
  border-left: ms(-18) solid $light-blue-color;

  p {
    margin: 0;
  }

  font-size: ms(0);
}

code {
  background: none;
  border-radius: 0;
  border: none;
  font-family: "SFMono-Regular",Menlo,Consolas,"Inconsolata",monospace;
  font-size: ms(0);
  margin: 0;
  padding: 0 5px;
  background-color: lighten($light-gray-color, 13%);
}

pre {
  -webkit-overflow-scrolling: touch;
  font-family: "SFMono-Regular",Menlo,Consolas,"Inconsolata",monospace;
  font-size: ms(0);
  margin: 0;
  padding: 5px 10px !important;
  overflow: auto;
  max-height: 60em;
  white-space: pre;
  border: 1px solid $light-gray-color;

  code {
    line-height: 1.75em;
    padding: 0 !important;
  }
}

strong {
  font-weight: bold;
}

em {
  font-style: italic;
}

ul,
ol,
dl {
    margin-top: 0;
    margin-left: 1.2em;
    margin-bottom: 1rem;
    list-style-type: square;
    list-style-position: outside;
    list-style-image: none;
}

div.footnotes {
  font-size: $footnote-font-size;
}

// Cookie consent
div.cc-window {
  font-size: .9rem;
}

.link {
  color: $blue-color;
  padding-bottom: .05em;
  border-bottom: 2px solid transparent;

  @include transition(border 300ms linear);

  &:hover,
  &:focus,
  &:active {
    border-bottom: 2px solid $blue-color;
  }
}

a {
  @extend .link;
}

.home-section-title {
  font-size: ms(1);
}